@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif; }

.track-card {
  background: #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  padding: 2rem 2rem 1.5rem;
  border-radius: 5px;
  margin-bottom: 1.5rem;
  max-width: 400px;
  height: calc(100% - 1.5rem); }
  .track-card p.label {
    font-size: .75rem;
    color: #17A2B8;
    margin: 0; }
  .track-card .button {
    text-decoration: none;
    background: #17A2B8;
    color: #fff;
    padding: .5rem 0 .5rem 1rem;
    border-radius: 50px;
    font-size: .75rem;
    -webkit-transition: all .3s ease;
    transition: all .3s ease; }
    .track-card .button i {
      margin-left: .5rem;
      opacity: 0;
      -webkit-transition: all .3s ease;
      transition: all .3s ease; }
    .track-card .button:hover {
      padding-right: 1rem; }
      .track-card .button:hover i {
        opacity: 1; }

.spinner {
  height: 60px;
  width: 60px;
  border: 10px solid;
  border-radius: 50px;
  border-color: #c3c3c3 transparent #c3c3c3 transparent;
  -webkit-animation: rotateSpinner 1s infinite;
          animation: rotateSpinner 1s infinite; }

@-webkit-keyframes rotateSpinner {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); } }

@keyframes rotateSpinner {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); } }

form {
  display: -webkit-flex;
  display: flex;
  max-width: 60%;
  margin: 0 auto; }
  form input {
    width: 100%;
    padding: .75rem 1.5rem;
    border-color: transparent;
    border-radius: 50px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
    margin-right: 2rem;
    -webkit-transition: all .3s ease;
    transition: all .3s ease; }
    form input:focus {
      outline: none;
      -webkit-transform: scaleX(1.05);
              transform: scaleX(1.05); }
  form button {
    padding: .75rem 2rem;
    border: none;
    border-radius: 50px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
    background: #17A2B8;
    color: #fff;
    -webkit-transition: all .4s ease;
    transition: all .4s ease; }
    form button:focus {
      outline: none; }
    form button:active {
      -webkit-transform: translateY(3px);
              transform: translateY(3px); }

.button-go-back {
  text-decoration: none;
  padding: .75rem 1.75rem;
  background: #17A2B8;
  color: #fff;
  border-radius: 50px; }
  .button-go-back:active, .button-go-back:visited {
    outline: none; }
  .button-go-back:hover {
    color: #fff;
    text-decoration: none; }

.container {
  margin-top: 2.5rem; }


